import {
  call,
  put,
  delay,
  takeEvery,
} from "redux-saga/effects";
import {
  fetchFilesRequest,
  deleteFileRequest,
  deleteFileSuccess,
  deleteFileFailure,
  setDeleteStatus,
} from "../reducers/fileManagerSlice";
import { deleteFileApi } from "../../api";

/* Saga       */
function* deleteFileSaga(action) {
  try {
    const { userId, fileIdToDelete } = action.payload;
    if (!userId || !fileIdToDelete) {
      throw new Error("userId  fileId   payload");
    }

    yield put(setDeleteStatus("pending")); //    Redux
    yield delay(500); //  0.5       
    yield call(deleteFileApi, userId, fileIdToDelete);
    yield put(deleteFileSuccess(fileIdToDelete)); //    Redux
    yield put(setDeleteStatus("succeeded")); //    Redux
    yield put(fetchFilesRequest(userId)); //     Redux
  } catch (error) {
    console.error(`Error deleting files: ${error.message}`);
    yield put(deleteFileFailure(error.message));
  }
}

export function* watchDeleteFileSaga() {
  yield takeEvery(deleteFileRequest.type, deleteFileSaga);
}
