import { call, put, takeEvery } from "redux-saga/effects";
import { fetchRegisterApi } from "../../api";
import {
  fetchRegisterRequest,
  fetchRegisterSuccess,
  fetchRegisterFailure,
} from "../reducers/registerSlice";
import Swal from "sweetalert2";

/* Saga   */
export function* fetchRegisterSaga(action) {
  try {
    const { data, status } = yield call(fetchRegisterApi, action.payload);
    if (status === 201) {
      yield Swal.fire({
        icon: "success",
        title: "  !",
        text: "  !",
      });
      yield put(fetchRegisterSuccess(data)); //    Redux
    } else {
      yield Swal.fire({
        icon: "error",
        title: "",
        text: data.message || "  !",
      });

      yield put(fetchRegisterFailure(data.message || " "));
    }
  } catch (error) {
    yield Swal.fire({
      icon: "error",
      title: "   ",
      text: ",   .",
    });

    yield put(fetchRegisterFailure("   "));
  }
}

export function* watchRegisterSaga() {
  yield takeEvery(fetchRegisterRequest.type, fetchRegisterSaga);
}
