import { call, put, takeEvery } from "redux-saga/effects";
import { fetchLoginApi } from "../../api";
import {
  fetchLoginRequest,
  fetchLoginSuccess,
  fetchLoginFailure,
} from "../reducers/loginSlice";
import Swal from "sweetalert2";

/* Saga     */
export function* fetchLoginSaga(action) {
  try {
    const { data, status } = yield call(fetchLoginApi, action.payload);
    if (status === 200) {
      yield Swal.fire({
        icon: "success",
        title: " !",
        text: "    !",
      });
      yield put(fetchLoginSuccess(data)); //    Redux
    } else {
      yield Swal.fire({
        icon: "error",
        title: "",
        text: data.message || "   !",
      });

      yield put(fetchLoginFailure(data.message || " "));
    }
  } catch (error) {
    yield Swal.fire({
      icon: "error",
      title: "   ",
      text: ",   .",
    });

    yield put(fetchLoginFailure("   "));
  }
}

export function* watchLoginSaga() {
  yield takeEvery(fetchLoginRequest.type, fetchLoginSaga);
}
